<template>
  <div>
    <group title="no placeholder">
      <selector title="" :options="list"></selector>
    </group>

    <group title="with placeholder">
      <selector placeholder="" title="" :options="list" @change="change"></selector>
    </group>

    <group title="without title">
      <selector placeholder="" :options="list"></selector>
    </group>
    
    <group title="set value=">
      <selector :selected.sync="selected1" :value.sync="value1" title="" :options="list"></selector>
    </group>

    <group title="readonly">
      <selector selected="" :readonly=true title="" :options="list"></selector>
    </group>

    <group title='multi selector'>
     <selector placeholder="" title="" :options="list" type="inline"></selector>
     <selector selected="" title="" :options="list"></selector>
    </group>
  </div>
</template>

<script>
import { Selector, Group } from '../components/'

export default {
  components: {
    Group,
    Selector
  },
  data: function () {
    return {
      list: [{ text: '', value: 'gd'}, {text: '', value: 'gx'}],
      selected1: '',
      value1: ''
    }
  },
  methods: {
    change: function (val) {
      console.log(val)
    }
  }
}
</script>


